/* animalCard CSS */
.animalCard {
  /* background: yellow; */
    background-image: url("https://images.freecreatives.com/wp-content/uploads/2016/02/Download-High-Res-Photoshop-Sublte-Vector-Patterns.jpg");
    background-size:cover;
    display: inline-block;
    border-radius: 2px;
    width: 700px;
    height: 300px;
    margin: 1rem;
    position: relative;
    padding:0;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}

.card-body {
    position: relative;
    padding: 1rem;
    height: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.animalCard > .content > ul {
    list-style-type: none;
    /* border: 5px solid red; */
}

.animalCard > .img-container {
height: 300px;
    width: auto;
    overflow: hidden;
    text-align: center;
    /* background: darkslategray; */
    background-repeat: round;
    float: left;
    clear: left;
    margin-right:25px;
}
.animalCard > .img-container > img {
  width: 100%;
  height:auto;
}

.animalCard > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
    float:left;
    display: contents;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.animalCard > .content > ul {
  list-style-type: none;
}

.animalCard > .content > ul > li {
  margin-top: 18px;
}




.animalCard > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.animalCard > .remove:hover {
  color: #d50000;
}

.card {
    position: absolute;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
    right: -50%;
    top: 0px;
}
#filterTriggerBtn {
    z-index: 1;
    background: blue;
    margin-top: 50px;
    width: 150px;
    height: 30px;
    border: none;
    color: white;
    border-radius: 5px;
}

element.style {
    z-index: 2;
    position: fixed;
    top: 0px;
    bottom: 0px;
    transition: -webkit-transform 0.3s ease-out 0s;
    will-change: transform;
    overflow-y: auto;
    left: 0px;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    box-shadow: rgba(0, 0, 0, 0.15) 2px 2px 4px;
}


.sideBarDiv {
    position: fixed;
    margin-top: 50px;
    width: 160px;
    margin: "350px 0 0 0";
